.app {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(
      rgba(255, 255, 255, 0.7),
      rgba(255, 255, 255, 0.3)
    ),
    url("https://images.pexels.com/photos/6146931/pexels-photo-6146931.jpeg?cs=srgb&dl=pexels-keira-burton-6146931.jpg");
  background-size: cover;
  background-position: center;

  form {
    background-color: white;
    padding: 0px 60px;
    border-radius: 10px;
  }

  .msg {
    color: rgb(77, 1, 77);
    text-align: center;
    font-size: 0.8rem;
  }
  .icon {
    text-align: center;
    font-family: "Italianno";
    font-style: normal;
    font-weight: 400;
    font-size: 85px;
  }

  button {
    width: 100%;
    height: 50px;
    padding: 10px;
    border: none;
    background-color: rebeccapurple;
    color: white;
    border-radius: 5px;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 30px;
  }
  .container {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
  }
  .error {
    font-size: 12px;
    padding: 3px;
    color: red;
  }
}
